.footer {
  width: 100%;
}

.footer__container {
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  background-color: #222233;
}
.footer__container .logo {
  padding-block: 0px;
}

.footer__contact {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  display: flex;
  justify-content: space-between;
  padding-block-start: 32px;
  padding-block-end: 48px;
}
.footer__contact > div {
  width: 291px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
@media (max-width: 1024px) {
  .footer__contact > div {
    max-width: 187px;
  }
}
@media (max-width: 768px) {
  .footer__contact {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 32px;
    padding-block: 48px;
    padding-block-end: 55px;
    column-gap: 32px;
    row-gap: 8px;
  }
  .footer__contact > div {
    width: 159px;
  }
}
@media (max-width: 400px) {
  .footer__contact {
    place-items: center;
    column-gap: 16px;
  }
  .footer__contact > div {
    width: 100px;
    white-space: nowrap;
  }
}

.footer__copy-title {
  color: #88888d;
  margin-block-start: -5px;
  letter-spacing: 0.5px;
}

.footer__copy {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .footer__copy {
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
  }
}

.footer__items {
  display: flex;
  align-items: center;
  gap: 24px;
}
@media (max-width: 768px) {
  .footer__items {
    gap: 16px;
  }
}

.footer__link {
  padding: 8.5px;
  border-radius: 50px;
}

.footer__link--green {
  background-color: #57efb4;
}

.footer__link img {
  width: 15px;
  height: 15px;
}

.footer .logo-link {
  gap: 8px;
}

.footer .logo__text {
  font-size: 27px;
  line-height: 100%;
}

.footer .logo__img {
  width: 44px;
  height: 44px;
}